* button {
  padding: 0;
  border: none;
  font: inherit;
  color: inherit;
  background-color: transparent;
  cursor: pointer;
  margin: 15px;
}
* a{
  font-size: 16px;
  font-weight: 500;
  color: #13B0F5;
  text-decoration: none;
  margin-right: 15px;

}
.header-dark{
  display: flex;
  flex-direction: column;
  height: 300px;
  background-size: 100% 250%;
  background-image: url('./img/supermen.jpg');
}
.header-ligth{
  display: flex;
  flex-direction: column;
  height: 300px;
  background-size:100% 250%;
  background-image: url('./img/oblivion.jpg');
}
/* header */
header{
  width: 100%;
  height: 45px;
  display: flex;
  justify-content: center;
  align-items: center
}
h3{
  font-size: 38px;
  background: linear-gradient(90deg, #13B0F5 -2.06%, #E70FAA 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  font-weight: 700;
}
header img{
  position: absolute;
  left: 0;
  width: 300px;
  margin-left: 25px;
  width: 35px;
  height: 35px;
}

/* navigate */
nav{
  display: flex;
  align-items: center;
  flex-direction: row-reverse;
  margin-right: 35px;
}


/* content */
.content{
  margin-top: 55px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

/* search */
.search {
  margin: auto;
  width: 700px;
  justify-content: space-between;
  display: flex;
  align-items: center;
}
.my-input{
    display: block;
    width: 500px;
    height: 38px;
    background-color: rgba(30, 30, 30, 1);
    padding: 5px;
    background-clip: padding-box;
    color:#13B0F5;
    font-size: 16px;
    border-radius: 30px;
    border: none;
    transition: border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
}
.my-input::placeholder{
  /* color: #13B0F5(255, 255, 255, 1); */
  font-size: 16px;
}
.my-input:focus{
    background-color: rgba(30, 30, 30, 1);
}
.my-input:hover{
    background-color: rgba(30, 30, 30, 1);
}
.search button{
  color: #13B0F5;
  background-color: rgba(30, 30, 30, 1);
  border-radius: 30px;
  width: 150px;
  height: 48px;
  cursor: pointer;
}
/* genres */

.genres a{
  color: #E70FAA;
  cursor: pointer;
}
/* card */
.cards{
  flex-wrap: wrap;
  width: 1000px;
  margin-top: 25px;
  display: flex;
  gap: 15px;
  height: 100%;
}
.card{
  margin-top: 25px;
  width: 150px;
  height: 200px;
  border: 1px solid black;
  border-radius: 15px;
  cursor: pointer;
}
.card img{
  width: 150px;
  height: 200px;
}
.card:hover{
  width: 150px;
  height: 200px;
  border: 1px solid #13B0F5;
  box-shadow: 0px 0px 20px #13B0F5;
  border-radius: 15px;
}
/* pagination */
.pagination{
  margin-top: 75px;
  display: flex;
  align-items: center;
}
/* theme */
.theme button {
  padding: 0;
  border: none;
  font: inherit;
  color: inherit;
  background-color: transparent;
  cursor: pointer;
  margin: 15px;
}
/* Loading */
.loading{
  margin: 150px auto;
  border: 10px solid gray;
  border-top: 10px solid #13B0F5;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  animation: spin 2s linear infinite;
}
@keyframes spin{
  0% {transform: rotate(0deg);}
  100% {transform: rotate(360deg);}
}
